@media (prefers-color-scheme: light) {
  /* Body */
  #page {
    --background-overall: #fafafa;
  }

  .btn-download {
    border: 1px solid #4e1e5d;
  }

  * {
    --color-text: #4e1e5d;
  }

  .skills {
    &__title {
      --color-text: #fafafa;
    }

    &__item {
      --color-text: #fafafa;
    }
  }

  .project {
    &__content {
      p {
        --color-text: #fafafa;
      }

      h3 {
        --color-text: #e0e0e0;
      }
    }
    &__subtitle {
      --color-text: #fafafa;
    }
  }
}

/* Switcher */

.switcher__radio {
  filter: invert(0);
}

.switcher__radio:checked {
  filter: invert(1);
}

.switcher__status {
  filter: invert(0);
}

html[data-theme='light'] {
  * {
    --color-text: #4e1e5d;
  }

  #page {
    --background-overall: #fafafa;
  }

  .burger-menu__button span,
  .burger-menu__button span::after,
  .burger-menu__button span::before {
    --color-burger: #4e1e5d;
  }

  .skills {
    &__title {
      --color-text: #fafafa;
    }

    &__item {
      --color-text: #fafafa;
    }
  }

  .project {
    &__content {
      p {
        --color-text: #fafafa;
      }

      h3 {
        --color-text: #e0e0e0;
      }
    }
    &__subtitle {
      --color-text: #fafafa;
    }
  }
}

.btn {
  --background-button: #4e1e5d;

  span {
    --color-text: #fafafa;
  }
}

.btn-download {
  border: 1px solid #4e1e5d;
}
