.main {
	margin-top: 2rem;
	align-items: center;

	.about-me {
		display: flex;
		flex-direction: column;
		margin: 0 auto;
		align-items: center;

		&__img-wrapper {
			position: relative;
			margin-top: auto;
			margin-bottom: 30px;
		}

		&__img {
			position: relative;
			width: 250px;
			height: 250px;
			border-radius: 50%;
		}

		&__title {
			font-size: 1.5rem;
		}

		&__education {
			font-size: 3rem;
		}

		&__buttons-wrapper {
			display: flex;
			width: 80%;
			flex-direction: row;
			justify-content: center;
			gap: 1em;
			margin-top: var(--margin-top);

			@media (max-width: 500px) {
				flex-direction: column;
				row-gap: 1rem;
				
			}
		}

		&__description {
			margin-top: 1.5rem;
			max-width: 1200px;

			.description__item {
				font-size: 1.2rem;
				line-height: 1.3;


				&::before {
					content: "<";
					font-size: 1.5rem;
					margin-left: -12px;
					color: #f18ba5;
				}

				&::after {
					content: "/>";
					font-size: 1.5rem;
					color: #f18ba5;
				}
			}

		}
	}

	@media (max-width: 800px) {
		text-align: center;
	}
}