@media (prefers-color-scheme: dark) {
  /* Body */
  #page {
    --background-overall: #1c121f;
  }

  * {
    --color-text: #e0e0e0;
  }

  /* Switcher */
  .switcher__radio {
    filter: invert(1);
  }

  .switcher__radio:checked {
    filter: invert(0);
  }

  .switcher__status {
    filter: invert(1);
  }
}

html[data-theme='dark'] {
  #page {
    --background-overall: #1c121f;
  }

  * {
    --color-text: #e0e0e0;
  }
}
